import React from 'react'
import Demo from './_Demo'
import DocumentTitle from 'react-document-title'
import { config } from 'config'

const SomeReactCode = React.createClass({
  statics: {
    metadata () {
      return {
        title: 'Навыки',
      }
    },
  },

  render () {
    return (
      <DocumentTitle title={`${SomeReactCode.metadata().title} | ${config.siteTitle}`}>
        <div>
          <h1>Мои навыки</h1>
          <p>
            Для создания веб-приложений использую язык <b>JavaScript</b>, на стороне сервера: <b>NodeJS</b> <a href="http://koajs.com/#introduction" target="_blank">(framework koa)</a>. При необходимости сохраняю данные в базах данных <b>MySQL</b> и <b>MongoDB</b> Собираю проекты с помощью систем сборки <b>Gulp</b> и <b>Webpack</b>. Для контроля версий исходного кода применяю систему <b>Git</b>.
          </p>
          <p>
            Родной язык - <b>русский</b>. Уровень <b>английского</b> - <i>Upper-Intermediate</i>. Круг моих интересов состоит из; <br />изучения иностанных языков и культур народов мира. <br /> люблю путешествовать, общаться и программировать.
          </p>
          <div
            style={{
              height: 500,
            }}
          >
            <Demo />
          </div>
        </div>
      </DocumentTitle>
    )
  },
})

export default SomeReactCode



